<template>
  <portal-dialog
    :visible="dialogVisible"
    :title="dialogTitle"
    width="800px"
    @show-dialog="showDialogFn"
    @cancel-btn-click="cancelFn"
    @confirm-btn-click="confirmFn"
  >
    <template #content>
      <!--dialog form -->
      <portal-form
        ref="searchList"
        :form-data="dialogSearchList"
        :formRules="dialogFormRules"
        :form-config-list="dialogFormConfigList"
        :searchBtnLoading="searchLoading"
        @common-enter-event="advancedSearch"
        @reset-btn-click="reset"
        @search-btn-click="advancedSearch"
        :smallAutoColumn="2"
        :bigAutoColumn="2"
      />
      <!-- dialog table -->
      <table-page
        class="table-page"
        size="small"
        :idKey="tableRowKey"
        :has-pagination="true"
        :columns="dialogColumns"
        :initializingLoading="true"
        :pagination="dialogPaginationConfig"
        :getTableData="getDialogTableData"
        :is-search="refreshTableFlag"
        :isCheck="true"
        :isSingleCheck="isSingleCheck"
        @single-selection-change="selectTableDataFn"
        @selection-change="selectsTableDataFn"
        :sortArr="dialogColumnsSortArr"
        :outSideTableData="tableData"
        @fetchData="tableFetchData"
      >
        <template slot="bankHeadOffice" slot-scope="scope">
          <span>{{ bankHeadOfficeMap[scope.data.row.bankHeadOffice] || scope.data.row.bankHeadOffice }}</span>
        </template>
        <template slot-scope="scope" slot="status">
          <div class="flex flex-align">
            <span
              v-if="scope.data.row.status !== ''"
              class="status-box"
              :style="statusStyle(scope.data.row.status)"
            ></span>
            <span>{{ showStatus(scope.data.row.status) }}</span>
          </div>
        </template>
      </table-page>
    </template>
  </portal-dialog>
</template>

<script>
import PortalDialog from '@/components/portal-dialog';
import PortalForm from '@/components/portal-form';
import TablePage from '@/components/tablePage';
import DIALOG_DATA from './dialog-data';
import {
  getCustomerList,
  searchCustomerIdentity,
  checkShortCode,
  getOrganizationList,
  getOrgOperList,
} from '../../../api/api-bulk';
import { formatDateStr } from '@/common/utils/date-time-utils.js';
import { isAllEmpty } from '@/common/utils/string-utils.js';
import { getOrgOperatorList } from '@/views/identifier-center/api/api-organization-operator';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';

export default {
  components: { PortalDialog, PortalForm, TablePage },
  name: 'FormItemClickDialog',
  props: {
    dialogVisible: {
      type: Boolean,
    },
    dialogType: {
      type: String,
    },
    simpleMode: {
      type: Boolean,
      default: () => false,
    },
    columns: {
      type: Array,
      default: null,
    },
    formConfigList: {
      type: Array,
      default: null,
    },
    getTableData: {
      type: Function,
      default: null,
    },
    tableRowKey: {
      type: String,
      default: '',
    },
    searchList: {
      type: Object,
      default: null,
    },
    formRules: {
      type: Object,
      default: null,
    },
    paginationConfig: {
      type: Object,
      default: null,
    },
    columnsSortArr: {
      type: Array,
      default: null,
    },
    title: {
      type: String,
      default: null,
    },
    isSingleCheck: {
      type: Boolean,
      default: true,
    },
    bankHeadOfficeMap: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      identityId: '',
      selectedTableData: {},
      selectedTableDatas: [],
      refreshTableFlag: false,
      searchLoading: false,
      tableData: [],
      customerSearchList: {
        queryCondition: 'msisdn',
        msisdn: '',
      },
      spOperatorSearchList: {
        userName: '',
        firstName: '',
        middleName: '',
        lastName: '',
        role: '',
        status: '',
        dateOfBirth: '',
      },
      organizationOperatorSearchList: {
        organizationShortCode: '',
        operatorId: '',
        userName: '',
        firstName: '',
        middleName: '',
        lastName: '',
        role: '',
        status: '',
        dateOfBirth: '',
      },
      organizationSearchList: {
        shortCode: '',
        organizationName: '',
        status: '',
      },
    };
  },
  computed: {
    statusStyle() {
      return (status) => {
        switch (status) {
          case '03':
            return { 'background-color': '#658DF9' };
          case '06':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case '07':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '00':
            return { 'background-color': '#658DF9' };
          case '02':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '04':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '05':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case '08':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          default:
            return;
        }
      };
    },
    dialogColumns() {
      if (this.columns) {
        return this.columns;
      } else if (this.dialogType === 'orgOperator') {
        return this.simpleMode ? DIALOG_DATA.spOperatorSimpleColumns : DIALOG_DATA.spOperatorColumns;
      } else {
        return [];
      }
    },
    dialogFormConfigList() {
      if (this.formConfigList) {
        return this.formConfigList;
      } else if (this.dialogType === 'orgOperator') {
        return this.simpleMode ? DIALOG_DATA.spOperatorSimpleFormConfigList : DIALOG_DATA.spOperatorFormConfigList;
      } else {
        return [];
      }
    },
    getDialogTableData() {
      if (this.getTableData) {
        return this.getTableData;
      } else if (this.dialogType === 'orgOperator') {
        return this.getOrgOperatorTableData;
      } else {
        return null;
      }
    },
    dialogSearchList() {
      if (this.searchList) {
        return this.searchList;
      } else if (this.dialogType === 'orgOperator') {
        return this.spOperatorSearchList;
      } else {
        return {};
      }
    },
    dialogFormRules() {
      if (this.formRules) {
        return this.formRules;
      } else if (this.dialogType === 'orgOperator') {
        return DIALOG_DATA.spOperatorFormRules;
      } else {
        return {};
      }
    },
    dialogPaginationConfig() {
      if (this.paginationConfig) {
        return this.paginationConfig;
      } else if (this.dialogType === 'orgOperator') {
        return DIALOG_DATA.spOperatorPaginationConfig;
      } else {
        return {};
      }
    },
    dialogColumnsSortArr() {
      if (this.columnsSortArr) {
        return this.columnsSortArr;
      } else if (this.dialogType === 'orgOperator') {
        return DIALOG_DATA.spOperatorColumnsSortArr;
      } else {
        return [];
      }
    },
    dialogTitle() {
      if (this.title) {
        return this.title;
      } else if (this.dialogType === 'orgOperator') {
        return 'Select Operator';
      } else {
        return '';
      }
    },
  },
  methods: {
    orgOperatorSearchCheck() {
      let firstName = this.dialogSearchList.firstName;
      let lastName = this.dialogSearchList.lastName;
      let middleName = this.dialogSearchList.middleName;
      let operatorName = this.dialogSearchList.userName;
      return isAllEmpty([firstName, lastName, middleName, operatorName]);
    },
    advancedSearch() {
      this.$refs.searchList.validate((valid) => {
        if (valid) {
          DIALOG_DATA.spOperatorPaginationConfig.pageIndex = 1;
          this.refreshTableFlag = true;
          if (this.orgOperatorSearchCheck()) {
            this.$modal.msgWarning(
              'Specify at least one search condition of First Name, Middle Name, Last Name, and User Name.'
            );
            this.refreshTableFlag = false;
            return false;
          }
        } else {
          return false;
        }
      });
    },
    reset() {
      this.$refs.searchList.resetFields();
      this.refreshTableFlag = true;
    },
    tableFetchData() {
      this.refreshTableFlag = false;
    },
    selectTableDataFn(value) {
      this.selectedTableData = value;
      if (this.dialogType === 'customer' || this.dialogType === 'maskCustomer') {
        this.selectedTableData.identityId = this.identityId;
      } else if (this.dialogType === 'organizationOperator' || this.dialogType === 'maskOrganizationOperator') {
        this.selectedTableData.identityId = value.orgOperatorId;
      } else if (this.dialogType === 'organization' || this.dialogType === 'maskOrganization') {
        this.selectedTableData.identityId = value.organizationId;
      }
    },
    selectsTableDataFn(val) {
      this.selectedTableDatas = val;
    },
    showDialogFn(val) {
      this.$emit('update:dialogVisible', val);
    },
    cancelFn() {
      this.$emit('update:dialogVisible', false);
    },
    confirmFn() {
      if (Object.keys(this.selectedTableData).length || this.selectedTableDatas.length > 0) {
        this.$emit('selected-data', this.selectedTableData, this.dialogType);
        this.$emit('selected-checked-data', this.selectedTableDatas, this.dialogType);
        this.$emit('update:dialogVisible', false);
      } else {
        this.$modal.msgError('Please select at least one record.');
      }
    },
    showStatus(status) {
      return STATUS_MAP[status] || '-';
    },

    // get customer data
    async getCustomerTableData() {
      let param = {};
      this.searchLoading = true;
      if (this.customerSearchList.queryCondition === 'msisdn') {
        param.msisdn = this.customerSearchList.msisdn;
        return new Promise((resolve) => {
          searchCustomerIdentity(param)
            .then((res) => {
              if (res.header && res.header.success) {
                if (res.identityId !== undefined) {
                  param.customerId = res.identityId;
                  this.identityId = res.identityId;
                  this.customerSearchList.identityId = res.identityId;
                  param.cpId = res.cpId;
                  resolve(this.searchCustomerInfo(param));
                } else {
                  this.$modal.msgError('The MSISDN does not exist');
                  resolve(this.searchCustomerInfo(param));
                }
              }
              resolve(res);
            })
            .catch(() => {
              resolve();
            })
            .finally(() => {
              this.refreshTableFlag = false;
              this.searchLoading = false;
            });
        });
      } else if (this.customerSearchList.queryCondition === 'identityId') {
        param.customerId = this.customerSearchList.msisdn;
        return new Promise((resolve) => {
          resolve(this.searchCustomerInfo(param));
        });
      }
    },
    searchCustomerInfo(param) {
      return new Promise((resolve) => {
        getCustomerList(param)
          .then((res) => {
            if (res.header && res.header.success) {
              if (res.msisdn) {
                this.identityId = param.customerId;
              }
              let tempObj = {
                msisdn: res.msisdn,
                status: res.status,
                customerType: res.customerType,
                dateOfBirth: formatDateStr(res.basicKYC.birthday),
                ...res.basicKYC,
              };
              res.list = [tempObj];
              res.totalRecords = res.list.length;
              resolve(res);
            } else {
              res.list = [];
              this.searchLoading = false;
              resolve(res);
              this.$modal.msgError(res.header.responseDesc);
            }
          })
          .catch(() => {
            resolve();
          })
          .finally(() => {
            this.searchLoading = false;
            this.refreshTableFlag = false;
          });
      });
    },

    // get org operator data
    getOrgOperatorTableData() {
      // 列表查询
      this.searchLoading = true;
      if (this.orgOperatorSearchCheck()) {
        this.searchLoading = false;
        this.refreshTableFlag = false;
        return false;
      }
      return new Promise((resolve) => {
        getOrgOperatorList({
          firstName: this.dialogSearchList.firstName,
          lastName: this.dialogSearchList.lastName,
          middleName: this.dialogSearchList.middleName,
          operatorName: this.dialogSearchList.userName,
          pageNo: DIALOG_DATA.spOperatorPaginationConfig.pageIndex,
          pageSize: DIALOG_DATA.spOperatorPaginationConfig.pageSize,
        })
          .then((res) => {
            if (res.header && res.header.success) {
              res.totalRecords = Number(res.totalRecord) || 0;
              resolve(res);
            }
          })
          .catch(() => {
            resolve();
          })
          .finally(() => {
            this.searchLoading = false;
            this.refreshTableFlag = false;
          });
      });
    },

    // get organization operator data
    getOrganizationOperatorTableData() {
      if (this.organizationOperatorSearchList.organizationShortCode === '') {
        this.$modal.msgWarning('The ShortCode cannot be empty.');
        this.refreshTableFlag = false;
        return false;
      }
      this.searchLoading = true;
      return new Promise((resolve) => {
        checkShortCode(this.organizationOperatorSearchList.organizationShortCode)
          .then((res) => {
            if (res.header && res.header.success) {
              if (res.check === 'Exist') {
                resolve(this.searchOrg());
              } else if (res.check === 'NotExist') {
                resolve();
                this.$modal.msgError('The Organization Short Code is not existed');
              } else if (res.check === 'Invalid') {
                resolve();
                this.$modal.msgError('The Organization Short Code is not invalid');
              }
            }
          })
          .catch(() => {
            resolve();
            this.$modal.msgError('The Organization Short Code Exception');
          })
          .finally(() => {
            this.refreshTableFlag = false;
            this.searchLoading = false;
          });
      });
    },
    searchOrg() {
      let paramsObj = {
        ...this.organizationOperatorSearchList,
        shortCode: this.organizationOperatorSearchList.organizationShortCode,
        operatorName: this.organizationOperatorSearchList.userName,
        pageNo: DIALOG_DATA.organizationOperatorPaginationConfig.pageIndex,
        pageSize: DIALOG_DATA.organizationOperatorPaginationConfig.pageSize,
      };
      return new Promise((resolve) => {
        getOrgOperList(paramsObj)
          .then((res) => {
            if (res.header && res.header.success) {
              res.list = res.orgOperatorInfoList || [];
              res.totalRecords = Number(res.totalSize) || 0;
              resolve(res);
            } else {
              res.list = res.orgOperatorInfoList || [];
              res.totalRecords = Number(res.totalSize) || 0;
              resolve(res);
            }
          })
          .catch(() => {
            resolve();
          })
          .finally(() => {
            this.searchLoading = false;
            this.refreshTableFlag = false;
          });
      });
    },

    // get organization data
    getOrganizationTableData() {
      let params = {
        ...this.organizationSearchList,
        pageNo: DIALOG_DATA.organizationPaginationConfig.pageIndex,
        pageSize: DIALOG_DATA.organizationPaginationConfig.pageSize,
      };
      this.searchLoading = true;
      return new Promise((resolve) => {
        getOrganizationList(params)
          .then((res) => {
            this.searchLoading = false;
            if (res.header && res.header.success) {
              res.list = [res.organizationInfo];
              if (!res.list[0]) {
                res.list = [];
              }
              res.totalRecords = res.list.length;
              resolve(res);
            }
          })
          .catch(() => {
            resolve();
          })
          .finally(() => {
            this.searchLoading = false;
            this.refreshTableFlag = false;
          });
      });
    },
  },
  watch: {
    dialogVisible: {
      handler(newVal) {
        // 切换表格类型时，重置数据,清空表单校验结果
        if (newVal) {
          this.tableData = [];
          Object.assign(this.$data, this.$options.data());
          if (this.$refs.searchList) {
            this.reset();
          }
        }
      },
    },
    'customerSearchList.queryCondition': {
      handler(newVal) {
        this.customerSearchList.msisdn = '';
        if (newVal === 'msisdn') {
          DIALOG_DATA.customerFormConfigList[1].label = 'MSISDN';
        } else if (newVal === 'identityId') {
          DIALOG_DATA.customerFormConfigList[1].label = 'Identity ID';
        }
      },
    },
  },
};
</script>

<style lang="scss" scoped>
/deep/.form-flex-container {
  padding: 0;
}
</style>
