/**
 * 判断传递进去的字符串数组中是否有空的字符串
 * @param arr
 * @returns {boolean}
 */
export function isAnyEmpty(arr) {
  for (let i = 0; i < arr.length; i++) {
    if (this.isEmptyString(arr[i])) {
      return true;
    }
  }
  return false;
}

/**
 * 判断传递进去的字符串数组中是否全部都是空字符串
 * @param arr
 * @returns {boolean}
 */
export function isAllEmpty(arr) {
  for (let i = 0; i < arr.length; i++) {
    if (!isEmptyString(arr[i])) {
      return false;
    }
  }
  return true;
}

/**
 * 判断字符串是否为空
 * @param val
 * @returns {boolean}
 */
export function isEmptyString(val) {
  return val === null || val === undefined || val.trim() === '';
}
