<template>
  <div class="business-portal-page">
    <portal-header :projectObj="titleConfig" />
    <portal-form
      ref="searchList"
      :form-data="formData"
      :form-rules="formRules"
      :form-config-list="formConfigList"
      :searchBtnLoading="searchLoading"
      @common-enter-event="search"
      @reset-btn-click="reset"
      @search-btn-click="search"
    />
    <div style="padding-left: 20px; margin-top: 20px">
      <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAddClick()">
        {{ $t('common.button.add') }}
      </el-button>
      <el-button
        id="suspend-task-plan-btn"
        :disabled="suspendDisabled"
        v-preventReClick
        size="small"
        @click="handleSuspendClick()"
      >
        {{ $t('BUC.taskPlan.suspend') }}
      </el-button>
      <el-button
        id="resume-task-plan-btn"
        :disabled="resumeDisabled"
        size="small"
        v-preventReClick
        @click="handleResumeClick()"
      >
        {{ $t('BUC.taskPlan.resume') }}
      </el-button>
      <el-button id="refresh-task-plan-btn" size="small" v-preventReClick @click="handleRefreshClick()">
        {{ $t('BUC.taskPlan.refresh') }}
      </el-button>
    </div>
    <div style="padding: 20px">
      <table-page
        size="small"
        :has-pagination="true"
        :isSearch="isSearch"
        :initializingLoading="true"
        :columns="taskPlanColumns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :isCheck="true"
        @selection-change="handleSelectionChange"
        :sortArr="sortArr"
        :customSorting="true"
        idKey="planID"
      >
        <template slot="bulkStatus" slot-scope="scope">
          <div class="flex flex-align">
            <span class="status-circle" :style="showStatus(bulkStatusMap[scope.data.row.bulkStatus])"></span>
            <span>{{ bulkStatusMap[scope.data.row.bulkStatus] }}</span>
          </div>
        </template>
        <template slot="scheduleTime" slot-scope="scope">
          {{ scope.data.row.scheduleTime }}
        </template>
        <template slot="operation" slot-scope="scope">
          <el-button
            v-if="formatStatus(scope.data.row.bulkStatus)"
            :id="'bulk-template-detail-' + scope.data.$index"
            type="text"
            size="small"
            @click="handleDetailClick(scope.data.row, 'details')"
          >
            {{ $t('common.button.detail') }}
          </el-button>
          <el-button
            v-else
            :id="'bulk-template-detail-' + scope.data.$index"
            type="text"
            size="small"
            @click="handleDetailClick(scope.data.row, 'process')"
          >
            {{ $t('BUC.taskPlan.process') }}
          </el-button>
        </template>
      </table-page>
    </div>

    <form-item-click-dialog
      dialogType="orgOperator"
      :simpleMode="true"
      :dialogVisible.sync="operatorSelectDialogVisible"
      @selected-data="fillSelectedData"
    >
    </form-item-click-dialog>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import PortalForm from '@/components/portal-form';
import TablePage from '@/components/tablePage';
import FormItemClickDialog from '../components/formItem-click-dialog.vue';
import {
  queryBulkTypes,
  queryTaskPlanList,
  queryBulkDataDicts,
  suspendTaskPlan,
  resumeTaskPlan,
} from '../../../api/api-bulk';
import { formatDateTimeStr3 } from '@/common/utils/date-time-utils';
import { mapGetters } from 'vuex';
import dayjs from 'dayjs';

export default {
  name: 'TaskPlanList',
  components: {
    PortalHeader,
    PortalForm,
    TablePage,
    FormItemClickDialog,
  },
  data() {
    return {
      titleConfig: {
        title: this.$t('BUC.taskPlan.pageTitle'),
        desc: this.$t('BUC.taskPlan.pageDesc'),
      },
      formData: {
        planName: '',
        bulkTypeID: null,
        status: null,
        scheduleTime: [
          dayjs(new Date(dayjs().year(), dayjs().month(), dayjs().date(), 0, 0, 0)).format('YYYYMMDDHHmmss'),
          dayjs(new Date(dayjs().year(), dayjs().month(), dayjs().date(), 23, 59, 59)).format('YYYYMMDDHHmmss'),
        ],
        createOperId: '',
        approveOperId: '',
        createdByName: '',
        approveOperName: '',
      },
      paginationConfig: {
        pageIndex: 1,
        pageSize: 10,
      },
      isSearch: false,
      searchLoading: false,
      sortArr: [
        'planID',
        'planName',
        'bulkTypeName',
        'fileTemplate',
        'scheduleTime',
        'timelyCompletion',
        'startTime',
        'bulkStatus',
      ],
      taskPlanColumns: [
        {
          label: this.$t('BUC.taskPlan.planID'),
          prop: 'planID',
          width: '200px',
          align: 'left',
          'sort-method': (a, b) => {
            return a.planID > b.planID ? 1 : -1;
          },
        },
        {
          label: this.$t('BUC.taskPlan.bulkTypeName'),
          prop: 'bulkTypeName',
          minWidth: '200px',
          align: 'left',
          'sort-method': (a, b) => {
            return a.bulkTypeName > b.bulkTypeName ? 1 : -1;
          },
        },
        {
          label: this.$t('BUC.taskPlan.planName'),
          prop: 'planName',
          minWidth: '200px',
          align: 'left',
          'sort-method': (a, b) => {
            return a.planName > b.planName ? 1 : -1;
          },
        },
        {
          label: this.$t('BUC.taskPlan.status'),
          prop: 'bulkStatus',
          __slotName: 'bulkStatus',
          minWidth: '200px',
          align: 'left',
          'sort-method': (a, b) => {
            return a.bulkStatus > b.bulkStatus ? 1 : -1;
          },
        },
        {
          label: this.$t('BUC.taskPlan.scheduleTime'),
          prop: 'scheduleTime',
          __slotName: 'scheduleTime',
          minWidth: '200px',
          align: 'left',
          'sort-method': (a, b) => {
            return a.scheduleTime > b.scheduleTime ? 1 : -1;
          },
        },
        {
          label: this.$t('BUC.taskPlan.startTime'),
          prop: 'startTime',
          minWidth: '200px',
          align: 'left',
          'sort-method': (a, b) => {
            return a.startTime > b.startTime ? 1 : -1;
          },
        },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          width: '100',
          fixed: 'right',
        },
      ],

      bulkTypeOptions: [],
      selectedPlanList: [],
      operatorSelectKey: 'createOperId',
      operatorSelectDialogVisible: false,

      suspendDisabled: true,
      resumeDisabled: true,
      planIDs: [],
    };
  },
  computed: {
    formRules() {
      return {};
    },
    formConfigList() {
      return [
        {
          label: this.$t('BUC.taskPlan.planName'),
          key: 'planName',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: this.$t('BUC.taskPlan.bulkTypeName'),
          key: 'bulkTypeID',
          comp: 'el-select',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
          options: this.bulkTypeOptions,
        },
        {
          label: this.$t('BUC.taskPlan.status'),
          key: 'status',
          comp: 'el-select',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            clearable: true,
          },
          options: this.bulkStatusOptions,
        },
        {
          formItemClass: 'org-short-code',
          label: this.$t('BUC.taskPlan.createdBy'),
          prop: 'createdByName',
          key: 'createdByName',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.select'),
            id: 'dialog_performedOnIdentity',
          },
          id: 'dialog_performedOnIdentity',
          event: {
            focus: () => {
              this.operatorSelectKey = 'createOperId';
              this.operatorSelectDialogVisible = true;
            },
          },
          slots: [
            {
              name: 'append',
              comp: 'el-link',
              class: 'short-code-icon',
              bind: {
                icon: 'el-icon-more',
                underline: false,
              },
              event: {
                click: () => {
                  this.operatorSelectKey = 'createOperId';
                  this.operatorSelectDialogVisible = true;
                },
              },
            },
          ],
        },
        {
          label: this.$t('BUC.taskPlan.approvedBy'),
          prop: 'approveOperName',
          key: 'approveOperName',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.select'),
            id: 'dialog_performedOnIdentity',
          },
          id: 'dialog_performedOnIdentity',
          event: {
            focus: () => {
              this.operatorSelectKey = 'approveOperId';
              this.operatorSelectDialogVisible = true;
            },
          },
          slots: [
            {
              name: 'append',
              comp: 'el-link',
              class: 'short-code-icon',
              bind: {
                icon: 'el-icon-more',
                underline: false,
              },
              event: {
                click: () => {
                  this.operatorSelectKey = 'approveOperId';
                  this.operatorSelectDialogVisible = true;
                },
              },
            },
          ],
        },
        {
          formItemClass: 'form-special-date-box',
          sameGroup: true,
          label: this.$t('BUC.taskPlan.scheduleTime'),
          prop: 'scheduleTime',
          comp: [
            {
              key: 'scheduleTime',
              comp: 'el-date-picker',
              bind: {
                align: 'right',
                type: 'datetimerange',
                format: 'dd-MM-yyyy HH:mm:ss',
                'range-separator': '~',
                'value-format': 'yyyyMMddHHmmss',
                'default-time': ['00:00:00', '23:59:59'],
                'start-placeholder': this.$t('common.placeholder.select'),
                'end-placeholder': this.$t('common.placeholder.select'),
              },
              event: {
                blur: () => {
                  this.choiceDate = '';
                },
              },
            },
            {
              comp: 'i',
              class: 'el-icon-date data_icon',
            },
          ],
        },
      ];
    },
    queryParams() {
      const { planName, bulkTypeID, status, scheduleTime, createOperId, approveOperId } = this.formData;
      const { pageIndex, pageSize } = this.paginationConfig;
      let startTime = '';
      let endTime = '';
      if (scheduleTime) {
        startTime = scheduleTime[0];
        endTime = scheduleTime[1];
      }
      return {
        planName,
        bulkTypeID,
        status,
        createOperId,
        approveOperId,
        startTime: startTime,
        endTime: endTime,
        offset: (pageIndex - 1) * pageSize,
        limit: pageSize,
      };
    },

    ...mapGetters('datadict', ['moduleDict']),
    bulkStatusOptions() {
      let hisStatus = this.moduleDict.bulk['config.bulk.his.status.list'].listWithAll;
      let status = this.moduleDict.bulk['config.bulk.plan.status.list'].listWithAll;
      if (status.length > 0) {
        status.shift();
      }
      return [...hisStatus, ...status];
    },
    bulkStatusMap() {
      let hisStatus = this.moduleDict.bulk['config.bulk.his.status.list'].objMap;
      let status = this.moduleDict.bulk['config.bulk.plan.status.list'].objMap;
      return Object.assign(hisStatus, status);
    },
  },
  methods: {
    search() {
      this.$refs['searchList'].validate((valid) => {
        if (valid) {
          // 点击search起始页应该为1
          this.paginationConfig.pageIndex = 1;
          this.searchLoading = true;
          this.isSearch = true;
        }
      });
    },

    async getTableData() {
      try {
        let res = await queryTaskPlanList(this.queryParams);
        if (res.header && res.header.success) {
          let { bulkPlanList } = res.data;
          bulkPlanList.forEach((element) => {
            element.scheduleTime = formatDateTimeStr3(element.scheduleTime);
            element.startTime = formatDateTimeStr3(element.startTime);
          });
          return {
            ...res,
            totalRecords: res.data.numberOfTotal,
            list: bulkPlanList || [],
          };
        }
      } catch (error) {
      } finally {
        this.isSearch = false;
        this.searchLoading = false;
      }
    },
    reset() {
      this.paginationConfig.pageIndex = 1;
      this.$refs.searchList.innerFormRef.resetFields();
      this.isSearch = true;
    },
    handleSelectionChange(val) {
      this.planIDs = [];
      if (val.length < 1) {
        this.suspendDisabled = true;
        this.resumeDisabled = true;
        return;
      }
      val.forEach((item) => {
        this.planIDs.push(item.planID);
      });
      this.selectedPlanList = val;
      this.planIDs = this.selectedPlanList.map((item) => item.planID);
      let statusList = [];
      val.forEach((item) => {
        statusList.push(item.bulkStatus);
      });
      if (val.length >= 0) {
        this.suspendDisabled = statusList.some((item) => {
          return item !== 'PROCESSING';
        });
      } else {
        this.suspendDisabled = false;
      }
      this.resumeDisabled = statusList.some((item) => {
        return item !== 'SUSPEND';
      });
    },

    handleDetailClick(row, type) {
      let params = {
        bulkPlanID: row.planID,
        mode: 'taskPlan',
        type: type,
        finishTime: row.finishTime,
        isShowNavigation: true,
        status: row.bulkStatus,
      };
      this.$router.push({
        path: '../../bulk/bulkTask/details',
        query: { params: encodeURIComponent(JSON.stringify(params)) },
      });
    },
    handleAddClick() {
      this.$router.push({
        path: '../../bulk/bulkTask/create',
      });
    },
    handleSuspendClick() {
      let message = '';
      if (this.planIDs.length > 1) {
        message = 'Are you sure to suspend these task plans ?';
      } else {
        let planName = this.selectedPlanList[0].planName;
        message = `Are you sure to suspend this task plan "${planName}" ?`;
      }
      this.$modal
        .confirm(message, 'Suspend Task Plan', {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          type: 'warning',
          closeOnClickModal: false,
        })
        .then(() => {
          suspendTaskPlan({ planIDs: this.planIDs })
            .then((res) => {
              if (res.header.success) {
                this.$modal.msgSuccess('Suspend successfully.');
                this.handleRefreshClick();
              }
            })
            .catch()
            .finally(() => {
              this.loading = false;
            });
        });
    },
    handleResumeClick() {
      let message = '';
      if (this.planIDs.length > 1) {
        message = 'Are you sure to resume these task plans ?';
      } else {
        let planName = this.selectedPlanList[0].planName;
        message = `Are you sure to resume this task plan "${planName}" ?`;
      }
      this.$modal
        .confirm(message, 'Suspend Task Plan', {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          type: 'warning',
          closeOnClickModal: false,
        })
        .then(() => {
          resumeTaskPlan({ planIDs: this.planIDs })
            .then((res) => {
              if (res.header.success) {
                this.$modal.msgSuccess('Resume successfully.');
                this.handleRefreshClick();
              }
            })
            .catch()
            .finally(() => {
              this.loading = false;
            });
        });
    },
    handleRefreshClick() {
      this.search();
    },
    async initBulkTypes() {
      await queryBulkTypes({ limit: 50, offset: 0 }).then((res) => {
        this.bulkTypeOptions = (res.data || []).map((type) => {
          return {
            ...type,
            value: type.id,
            label: type.bulkTypeName,
          };
        });
      });
      this.bulkTypeOptions.unshift({ value: null, label: 'All', status: 'Y', enabled: true });
    },
    fillSelectedData(value) {
      if (this.operatorSelectKey === 'approveOperId') {
        this.formData.approveOperName = value.userName;
        this.formData.approveOperId = value.identityId;
      }
      if (this.operatorSelectKey === 'createOperId') {
        this.formData.createdByName = value.userName;
        this.formData.createOperId = value.identityId;
      }
    },
    async initDataDict() {
      this.$store.dispatch('datadict/getModuleDicts', {
        moduleName: 'bulk',
        dictCodes: ['config.bulk.plan.status.list', 'config.bulk.his.status.list'],
        queryAPI: queryBulkDataDicts,
      });
    },
    formatStatus(val) {
      let bulkStatusMap = [];
      let status = this.moduleDict.bulk['config.bulk.plan.status.list'].objMap;
      for (let item in status) {
        bulkStatusMap.push(item);
      }
      if (bulkStatusMap.includes(val)) {
        return false;
      } else {
        return true;
      }
    },
    showStatus(val) {
      switch (val) {
        case 'Initial':
          return `background-color:${this.GlobalStyleVariables.backgroundColor};border:1px solid #E0E0E0;`;
        case 'Processing':
          return { 'background-color': this.GlobalStyleVariables.statusNoticeColor };
        case 'Checking':
          return `background-color:${this.GlobalStyleVariables.backgroundColor};border:1px solid ${this.GlobalStyleVariables.initialColor}`;
        case 'Suspend':
          return { 'background-color': this.GlobalStyleVariables.statusColorwarning };
        case 'Wait Process':
          return { 'background-color': this.GlobalStyleVariables.statusColorwarning };
        case 'Wait Active':
          return { 'background-color': this.GlobalStyleVariables.statusColorwarning };
        case 'Dis Active':
          return { 'background-color': this.GlobalStyleVariables.statusDark };
        case 'Failed':
          return { 'background-color': this.GlobalStyleVariables.statusErrorColor };
        case 'Expire':
          return { 'background-color': this.GlobalStyleVariables.statusDark };
        case 'Completed':
          return { 'background-color': this.GlobalStyleVariables.statusSuccessColor };
        case 'Canceled':
          return { 'background-color': this.GlobalStyleVariables.statusDark };
        case 'Deactive':
          return { 'background-color': this.GlobalStyleVariables.statusDark };
        default:
          break;
      }
    },
  },
  created() {
    this.initBulkTypes();
    this.initDataDict();
  },
};
</script>

<style></style>
